import Vue from 'vue'
import Vuex from 'vuex'
import request from '../axios/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    swiperList: [],
    detail: JSON.parse(sessionStorage.getItem("detail")) || "",
    id: JSON.parse(sessionStorage.getItem("id")) || '',
    audioObject: {
      progress: 0,
      isPlay: false,
      duration: 0,
      currentTime: 0,
      ele: ""
    },
    playList: JSON.parse(sessionStorage.getItem("playList")) || [],
    showPopup: false,
    showModeList: false,
    Mode: "listCircle",
    currentMusicIndex: 0
  },
  mutations: {
    swiperMutations(state, payload) {
      state.swiperList = payload
    },
    saveDetailId(state, payload) {
      sessionStorage.setItem("id", JSON.stringify(payload))
      state.id = payload
    },
    saveDetail(state, payload) {
      sessionStorage.setItem("detail", JSON.stringify(payload)) //解决在原地刷新时传递的参数丢失的问题
      state.detail = payload
    },
    setDurationTime(state, payload) {
      state.audioObject.duration = payload
    },
    setCurrentTime(state, payload) {
      state.audioObject.currentTime = payload

    },
    setPlayStatus(state, payload) {
      if (typeof payload === "boolean") {
        state.audioObject.isPlay = payload
      } else {
        state.audioObject.isPlay = !state.audioObject.isPlay;
      }
      state.audioObject.isPlay === true
        ? state.audioObject.ele.play()
        : state.audioObject.ele.pause();
    },
    setPlayEle(state, payload) {
      state.audioObject.ele = payload
    },
    //这是添加单个歌曲
    setPlayList(state, payload) {
      let flag
      flag = state.playList.find(item => item.sound.id === payload.sound.id)
      !flag === true ? state.playList.push(payload) : null
      sessionStorage.setItem("playList", JSON.stringify(state.playList))
    },
    //这是一键添加所有歌曲
    setPlayListAll(state, payload) {
      let playList = JSON.parse(JSON.stringify(payload))
      sessionStorage.setItem("playList", JSON.stringify(playList))
      // state.playList = payload
    },
    handleModeList(state, payload) {
      state.showModeList = payload
    },
    handlePopup(state, payload) {
      state.showPopup = payload
    },
    changeModeStatus(state, payload) {
      state.Mode = payload
    },
    removePlayListItem(state, payload) {
      state.playList.splice(payload, 1)
    },
  },
  actions: {
    async swiperActions({ commit }) {
      const swiperList = await request("/banner")
      commit("swiperMutations", swiperList)

    }
  },
  getters: {
    getProgress(state) {
      if (state.audioObject.duration && state.audioObject.currentTime) {
        return Math.floor((state.audioObject.currentTime / state.audioObject.duration) * 100) + "%"
      }

    }
  },
  modules: {
  }
})
