import Vue from 'vue'
const filters = {
  formatTime(time) {
    time = Math.round(time)
    let m = Math.floor(time / 60)
    let s = time % 60
    m = m < 10 ? "0" + m : m
    s = s < 10 ? "0" + s : s
    return `${m}:${s}`
  }
}
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})