<template>
  <div class="popup" :class="{active:showPopup}" @click="handleMask">
    <transition name="fade">
      <div class="popup-container" :class="{active:showPopup}">
        <div class="top f_ai">
          <span>清空</span>
          <span>播放列表({{playList.length}})</span>
          <span class="iconfont icon-ms" @click.stop="showMode"></span>
        </div>
        <div class="bottom">
          <div class="content f_ai" v-for="(item,index) in playList" :key="item.sound.id">
            <div class="left">
              <!-- 这是动态添加歌曲前面的小图标 -->
              <span class="iconfont" :class="{'icon-yinle':(id===item.sound.id)}"></span>
              <span
                class="name ellipsis"
                @click="changeMusic(item.sound.id,index,item)"
              >{{item.sound.name}}</span>
            </div>
            <div class="right iconfont icon-sc" @click.stop="removeSong(item.sound.id)"></div>
          </div>
        </div>

        <div class="mode" :class="{active:showModeList}">
          <div class="modeList">
            <div
              class="mode-item f_ai"
              :class="item.mode===Mode?'active':''"
              v-for="(item,index) in modeList"
              :key="index"
              @click.stop="changeMode(index)"
            >
              <span>{{item.name}}</span>
              <span class="iconfont" :class="item.icon"></span>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  mounted() {},
  data() {
    return {
      modeList: [
        { name: "默认", icon: "icon-ms", mode: "listCircle" },
        { name: "随机播放", icon: "icon-sj", mode: "random" },
        { name: "单曲循环", icon: "icon-dq", mode: "singleCircle" },
        { name: "列表循环", icon: "icon-lb", mode: "listCircle" }
      ]
    };
  },
  computed: {
    ...mapState([
      "showPopup",
      "showModeList",
      "Mode",
      "playList",
      "currentMusicIndex",
      "id"
    ])
  },
  methods: {
    ...mapMutations([
      "handleModeList",
      "handlePopup",
      "changeModeStatus",
      "removePlayListItem",
      "setCurrentMusicIndex",
      "saveDetailId",
      "saveDetail"
    ]),
    // 关于遮罩层操作
    handleMask() {
      this.handlePopup(false);
      this.handleModeList(false);
    },
    // 显示模式选择列表
    showMode() {
      this.handleModeList(!this.showModeList);
    },
    // 切换播放模式
    changeMode(id) {
      let mode = this.handleMode(id);
      this.changeModeStatus(mode);
      this.handlePopup(false);
    },
    // 传递不同的值选择不同的播放模式
    handleMode(id) {
      switch (id) {
        case 0:
          return "listCircle";
        case 1:
          return "random";
        case 2:
          return "singleCircle";
        case 3:
          return "listCircle";
      }
    },
    //点击切换播放列表里面的某一首歌曲
    // 这里的思路原本我是想通过传递不同的id重新获取数据,
    //但由于在同一个路径下虽然路上传递的参数显示改变了但实际上并不能通过route获得传递过来的参数
    //所以这里直接通过将点击当前detail项直接保存到vuex中,因为详情页的数据就是通过detail的数据来渲染的
    changeMusic(id, index, item) {
      this.saveDetailId(id);
      // this.setCurrentMusicIndex(index);
      this.saveDetail(item);
      // 这里要用replace因为如果用push的话,虽然路径变了但是返回的话就要返回两次
      this.$router.replace({ name: "detail", query: { id } });
    },
    //移除播放列表的某只歌曲
    removeSong(id) {
      this.playList.forEach((item, index) => {
        if (item.sound.id === id) {
          this.removePlayListItem(index);
          return;
        }
      });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.popup {
  position: fixed;
  width: 100%;
  height: 0;
  // height: 100%;
  left: 0;
  bottom: 0;
  font-size: 14px;
  transition: all 0.3s;
  z-index: 221;
  &.active {
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
  }
  .popup-container {
    position: absolute;
    bottom: 0px;
    left: 0;
    width: 100%;
    height: auto;
    background-color: #fff;
    opacity: 0;
    transition: all 0.5s;
    z-index: 333;

    &.active {
      opacity: 1;
      bottom: 50px;
    }

    .top {
      padding: 5px;
      height: 35px;
      justify-content: space-around;
      color: @shallowGreen;
      .icon-ms {
        font-size: 23px;
      }
    }
    .bottom {
      // 超过两百显示滚动条
      height: 200px;
      overflow-y: scroll;

      .content {
        padding: 5px 10px;
        justify-content: space-between;
        height: 40px;
        .left {
          .icon-yinle {
            color: @deepGreen;
            font-size: 16px;
          }
          .name {
            color: #333333;
            margin-left: 3px;
          }
        }
      }
    }

    .mode {
      width: 100%;
      z-index: -1;
      // height: auto;
      flex-direction: column;
      padding: 10px;
      position: absolute;
      left: 0;
      bottom: 0;
      background-color: #fff;
      box-sizing: border-box;
      font-size: 16px;
      border-top: 1px solid #f4f4f4;
      transition: all 0.5s;
      opacity: 0;
      &.active {
        height: auto;
        // bottom: 50px;
        opacity: 1;
        z-index: 330;
      }
      .mode-item {
        width: 100%;
        height: 40px;
        justify-content: space-between;
        color: #757575;
        &.active {
          color: @deepGreen;
        }
        .iconfont {
          font-size: 16px;
        }
      }
    }
  }
}
</style>