<template>
  <div class="swiper">
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="(item,index) in swiperList" :key="index" class="swiper-item">
        <div class="image-container" @click="toDetail(item.sound.id)">
          <img :src="item.sound.pic_640" alt />
        </div>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { getSwiper } from "../axios/api";
import { mapState } from "vuex";
export default {
  name: "Swiper",
  async mounted() {
    let swiperList = await getSwiper();
    this.swiperList = swiperList;
    // this.$store.dispatch("swiperActions");
  },
  data() {
    return {
      swiperList: []
    };
  },
  computed: {
    // ...mapState(["swiperList"])
  },
  methods: {
    toDetail(id) {
      this.$router.push({ name: "detail", params: { id } }); //这里params直接简写就行默认会认为键值的方式
    }
  }
};
</script>

<style lang="less" scoped>
.swiper {
  width: 100%;
  height: 4rem;
  .my-swipe {
    width: 100%;
    height: 100%;
    .swiper-item {
      width: 100%;
      height: 100%;
      .image-container {
        width: 100%;
        height: 100%;
        img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
</style>