<template>
  <div class="control-bar">
    <div class="container f_ai">
      <audio :src="detail.sound.source" ref="audio"></audio>

      <div class="left f_ai" @click="toDetail(detail.sound.id)">
        <img :src="detail.sound.pic_500" alt />
        <div class="info">
          <div class="song-name">{{detail.sound.name}}</div>
          <div class="user-name">{{detail.sound.user.name}}</div>
        </div>
      </div>
      <div class="right f_ai" @click="handleControlBtn">
        <div class="menu iconfont icon-yylb"></div>
        <div class="play iconfont" :class="audioObject.isPlay?'icon-stop':'icon-start'"></div>
        <div class="next iconfont icon-next" @click="handleNext"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import { format } from "../utils/index";
export default {
  name: "controlBar",
  data() {
    return {
      status: false
    };
  },

  mounted() {
    this.setTime();
    if (!this.audioObject.ele) {
      this.setPlayEle(this.$refs.audio);
    }
  },
  computed: {
    ...mapState([
      "detail",
      "audioObject",
      "showPopup",
      "showModeList",
      "Mode",
      "currentMusicIndex",
      "playList",
      "id"
    ])
  },
  methods: {
    ...mapMutations([
      "setDurationTime",
      "setCurrentTime",
      "setPlayStatus",
      "setPlayEle",
      "handleModeList",
      "handlePopup",
      "setCurrentMusicIndex",
      "saveDetail",
      "saveDetailId"
    ]),
    toDetail(id) {
      this.$router.push({ name: "detail", query: { id } });
    },
    handleControlBtn(e) {
      // 当点击播放按钮时
      if (e.target.className.includes("play")) {
        this.setPlayStatus();
      }
      // 当点击菜单按钮时
      else if (e.target.className.includes("menu")) {
        this.handleModeList(false);
        this.handlePopup(!this.showPopup);
      } else {
        //当点击下一首的时候
      }
    },
    // 设置时间
    setTime() {
      this.$nextTick(() => {
        //当歌曲可以播放时
        this.audioObject.ele.oncanplay = () => {
          this.setPlayStatus(this.status);

          //这是解决在详情刷新的时候播放图标的不对
          //this.audioObject.ele.play(); //一定要手动执行以下,不然的话不会触发ontimeupdate事件
          let duration = this.audioObject.ele.duration;
          this.setDurationTime(duration);
        };
        //当歌曲正在播放时
        this.audioObject.ele.ontimeupdate = () => {
          let currentTime = this.audioObject.ele.currentTime;

          this.setCurrentTime(currentTime);
        };
        // 当前歌曲播放完成时
        this.handleSongEnd();
      });
    },
    //当前歌曲播放完毕后的下一步操作
    handleSongEnd() {
      this.audioObject.ele.onended = () => {
        this.MusicEndOrNext();
      };
    },
    // 当点击下一首
    handleNext() {
      this.MusicEndOrNext();
    },
    //当音乐完或者点击下一首时的逻辑操作
    MusicEndOrNext() {
      let current;
      // 列表循环
      if (this.Mode === "listCircle") {
        current = this.playList.findIndex(item => {
          return item.sound.id === this.id;
        });
        if (current === this.playList.length - 1) {
        } else {
          // 下面解决返回的数据格式为这个 __ob__: Observer
          let detail = JSON.parse(JSON.stringify(this.playList[++current]));
          this.saveDetail(detail);
          this.saveDetailId(detail.sound.id);
        }
      }
      // 随机播放
      else if (this.Mode === "random") {
        let randomNumber = Math.floor(Math.random() * this.playList.length);
        if (randomNumber === 0) {
          ++randomNumber;
          return;
        } else if (randomNumber === this.playList.length - 1) {
          --randomNumber;
          return;
        }
        let detail = JSON.parse(JSON.stringify(this.playList[randomNumber]));
        this.saveDetail(detail);
        this.saveDetailId(detail.sound.id);
      }
      // 单曲循环
      else if (this.Mode === "singleCircle") {
        this.audioObject.ele.load(); //这是audio标签重新加载方法
      }
      this.setPlayStatus(false);
    }
  },
  watch: {
    $route(to, from) {
      if (to.query.id === this.id) {
        this.status = false;
      } else {
        this.status = true;
      }
    }
    // "audioObject.isPlay"(val) {
    //   this.$nextTick(() => {
    //     if (!val && this.audioObject.ele) {
    //       this.audioObject.ele.pause();
    //     } else {
    //       this.audioObject.ele.play();
    //     }
    //   });
    // },
    // deep: true
  }
};
</script>

<style lang="less" scoped>
.control-bar {
  position: fixed;
  left: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.8);
  padding: 5px;
  box-sizing: border-box;
  border-top: 1px solid #f4f4f4;
  z-index: 222;
  width: 100%;
  font-size: 12px;
  height: 50px;
  color: #333;
  // padding-bottom: 0;
  .container {
    height: 100%;
    justify-content: space-between;

    .left {
      height: 100%;

      .info {
        flex-direction: column;
        padding: 2px;
        box-sizing: border-box;
        .song-name {
          height: 26px;
          margin-bottom: 3px;
        }
      }
      img {
        width: 34px;
        height: 34px;
        margin-right: 5px;
      }
    }
    .right {
      .iconfont {
        font-size: 22px;
        width: 32px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        border: 1px solid #333;
      }
      .menu {
      }
      .play {
        font-size: 38px;
        margin: 0 10px;
        // width: 38px;
        // height: 38px;
        box-sizing: border-box;
        border: 0;
      }
      .next {
      }
    }
  }
}
</style>