<template>
  <div class="list">
    <div class="banner f_center" v-if="banner">
      <span>{{banner}}</span>
    </div>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <div class="list-container">
        <div
          class="list-item"
          v-for="(item,index) in listData"
          :key="index"
          @click="toDetail(item.sound.id)"
        >
          <div class="image-container">
            <van-image :src="item.sound.pic_500" class="image" :key="index" />
          </div>
          <div class="info">
            <div class="title">{{item.sound.name}}</div>
            <div class="type">
              <span class="collection">{{item.sound.channel.name}}</span>
              <span class="channel">频道</span>
            </div>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getList } from "../axios/api";
export default {
  name: "list",
  mounted() {},
  data() {
    return {
      loading: false,
      finished: false
    };
  },
  props: {
    listData: {
      type: Array,
      default: () => []
    },
    banner: {
      type: String,
      default: ""
    }
  },
  methods: {
    onLoad() {
      this.$emit("getListData"); //当滚动的时候通知首页父组件的方法去加载
      this.$emit("getOtherList"); //当滚动的时候通知详情页父组件的方法去加载
    },
    toDetail(id) {
      this.$router.push({ name: "detail", query: { id } });
    }
  }
};
</script>

<style lang="less" scoped>
.list {
  background-color: #fff;
  .list-container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    .list-item {
      width: 50%;
      padding: 5px;
      box-sizing: border-box;
      // /deep/ .van-grid-item__content {
      //   padding: 10px 8px !important;
      // }
      .image-container {
        width: 100%;
        height: 175px;
        .image {
          width: 100%;
          height: 100%;
        }
      }
      .info {
        margin-top: 3px;
        width: 100%;
        font-size: 13px;
        .title {
          color: #555;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        .type {
          margin-top: 5px;
          display: flex;
          justify-content: space-between;
          .collection {
            color: #7dd97f;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            flex: 1;
          }
          .channel {
            color: #999;
          }
        }
      }
    }
  }
  .banner {
    height: 60px;
    color: @shallowGreen;
    font-size: 15px;
    span {
      padding: 5px 7px;
      border-bottom: 1px solid @shallowGreen;
    }
  }
}
</style>