<template>
  <div class="index">
    <Swiper></Swiper>
    <all-play :listData="list"></all-play>
    <List :listData="list" @getListData="getListData" ref="list"></List>
  </div>
</template>

<script>
import Swiper from "../components/swiper";
import List from "../components/list";
import AllPlay from "../components/allPlay";
import { getList } from "../axios/api";
export default {
  name: "Index",
  data() {
    return {
      page: 1,
      list: []
    };
  },
  components: {
    Swiper,
    List,
    AllPlay
  },
  mounted() {
    this.getListData(this.page);
  },
  methods: {
    // 接收并触发子组件传过来的方法
    async getListData(page) {
      if (this.page > 2) {
        this.$refs.list.finished = true;
        return;
      }
      this.$refs.list.loading = true;
      let list = await getList(this.page);
      this.list = [...this.list, ...list];
      this.$refs.list.loading = false;

      this.page++;
    }
  }
};
</script>
