<template>
  <div class="allPlay" :class="{full:isFull}" @click="handlePlayAll">
    <!-- 障眼法:下面的icon是假的,作为最开始显示的,当点击后就会隐藏 -->
    <div class="fake-icon" v-show="!isFull">
      <span class="iconfont icon-yinle"></span>
    </div>
    <div class="container" v-show="isFull">
      <span class="icon iconfont icon-yinle"></span>
      <span class="text">一键播放</span>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  mounted() {},
  data() {
    return {
      isFull: false,
      list: 0
    };
  },
  props: {
    listData: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    ...mapMutations(["setPlayListAll", "saveDetail", "saveDetailId"]),
    handlePlayAll() {
      //只有展开全部才能用一键添加功能
      if (!this.isFull) {
        this.isFull = !this.isFull;
        //5秒后自动回去
        setTimeout(() => {
          this.isFull = false;
        }, 5000);
        return;
      } else {
        this.setPlayListAll(this.listData);
        this.saveDetail(this.listData[0]);
        this.saveDetailId(this.listData[0].sound.id);
        this.isFull = !this.isFull;
      }
    }
  },
  computed: {
    ...mapState(["playList"])
  }
};
</script>

<style lang="less" scoped>
.allPlay {
  width: 30px;
  height: 30px;
  background-color: #6ed56c;
  font-size: 14px;
  border-radius: 40px;
  position: absolute;
  top: 155px;
  z-index: 22;
  overflow: hidden;
  transition: all 0.5s;
  &.full {
    width: 100px; //当隐藏fake-icon后要将这个width变为100px
  }
  .fake-icon {
    width: 100%;
    height: 100%;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    span {
      font-size: 22px;
    }
  }
  .container {
    color: white;
    margin: 0 auto; //让盒子居中
    width: 100px; //这里要设置100px,不然的话就装不下.text盒子,当显示的时候就会先排成竖着排再横着拍,视觉看起来很奇怪
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center; //让内容居中

    .icon {
      background-color: #fff;
      color: #6ed56c;
      border-radius: 50%;
    }
    .text {
      display: block;
      margin-left: 5px;
    }
  }
}
</style>